import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import FormGenerator from '../components/forms/FormGenerator';
import { Mutation, Query } from "react-apollo";
import { LOGIN } from '../graphql/mutation/remote';
import { LOCALLOGIN } from '../graphql/mutation/local';
import { LOCALLOGIN as LOCALLOGIN_QUERY } from '../graphql/query/local';

class Login extends Component {
    render() {        
        return (
            <Query query={LOCALLOGIN_QUERY}>
                {
                    ({ data }) => {
                        console.log("LOGIN QUERY data is ", data);
                        if (data.localLogin && data.localLogin.token) {
                            return (
                                <Redirect to="/"></Redirect>
                            );
                        }
                        return (
                            <Mutation mutation={LOGIN}>
                                {() => (
                                    <Mutation mutation={LOCALLOGIN}>
                                        {(locallogin) => {
                                            const defaults = {
                                                username: {
                                                    type: "text",
                                                    default: "",
                                                },
                                                password: {
                                                    type: "password",
                                                    default: "",
                                                },
                                            };
                                            return (
                                                <FormGenerator
                                                    defaults={defaults}
                                                    submitText="Login"
                                                    mutation={LOGIN}                                                    
                                                    postMutation={({ data }) => {
                                                        const { login: { token } } = data;
                                                        window.localStorage.setItem('userToken', token);
                                                        locallogin({ variables: { token } }).then(({ data }) => {
                                                            console.log(data);                                                            
                                                        });
                                                    }}
                                                    title="Login"
                                                    {...this.props}
                                                >
                                                </FormGenerator>                                                
                                            );
                                        }}
                                    </Mutation>
                                )}
                            </Mutation>
                        )
                    }
                }
            </Query>
        );
    }
}

export default Login;



